.search {
    .form-horizontal {
        display: inline-block;
    }
}

#editor-container {
    line-height: 23px;
    font-size: 16px;
    letter-spacing: 2px;
    font-weight: 500;
    font-family: "Microsoft YaHei",微软雅黑,宋体;
    outline: 0;
    padding: 5px;
    border-radius: 5px;
    width: 100%;
    min-height: 500px;
}
.column-item {
    width: 300px;
    display: inline-block;
    position: relative;
    height: 100px;
    background-color: #fff;
    margin: 10px;
    .icon {
        position: absolute;
        width: 100px;
        height: 100px;
        text-align: center;
        background-color: #333;
        color: #fff;
        padding-top: 20px;
        font-size: 40px;
    }
    .content {
        margin-left: 100px;
        padding: 20px 10px;
    }
}

.page-title {
    h1 {
        display: inline-block;
    }
    .language-toggle {
        display: inline-block;
    }
}